import React, { Component } from "react";
import styled from "styled-components";

const ConBox = styled.div`
  padding-top: 15%;
  text-align: center;
  p {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: 22px;
    color: #6e7783;
  }
  h2:after {
    content: "";
    display: block;
    width: 100px;
    margin: auto;
    border-bottom: 5px solid #77aaad;
    padding-bottom: 50px;
    margin-bottom: 60px;
  }
  h2 span:nth-child(2) {
    animation-delay: 0.05s;
  }
  h2 span:nth-child(3) {
    animation-delay: 0.1s;
  }
  h2 span:nth-child(4) {
    animation-delay: 0.15s;
  }
  h2 span:nth-child(5) {
    animation-delay: 0.2s;
  }
  h2 span:nth-child(6) {
    animation-delay: 0.25s;
  }
  h2 span:nth-child(7) {
    animation-delay: 0.3s;
  }
  h2 span:nth-child(8) {
    animation-delay: 0.35s;
  }
  h2 span:nth-child(9) {
    animation-delay: 0.4s;
  }

  h2 span {
    font-size: 100px;
    font-family: raleway;
    color: #77aaad;
    opacity: 0;
    transform: translate(-300px, 0);
    animation: animation 0.5s forwards;
  }

  @keyframes animation {
    60% {
      transform: translate(20px, 0);
    }
    90% {
      color: #fff;
    }
    100% {
      opacity: 1;
    }
  }
`;
class Contact extends Component {
  render() {
    return (
      <ConBox>
        <h2>
          <span>T</span>
          <span>H</span>
          <span>A</span>
          <span>N</span>
          <span>K</span>
          <span>&nbsp;&nbsp;Y</span>
          <span>O</span>
          <span>U</span>
          <span>&nbsp;!</span>
        </h2>
        <p>010-7189-6838 / yeol0324@naver.com</p>
      </ConBox>
    );
  }
}

export default Contact;
