import React from "react";
import styled from "styled-components";
import { Icon } from "semantic-ui-react";

const ButtonStyle = styled.div`
  padding: 50px;
  color: #fff;
  transition: all 0.3s;
  position: relative;
  filter: drop-shadow(0 0 3px #9dc3c1);
  cursor: pointer;
  &:hover {
    transform: scale(1.1);
    color: #77aaad;
  }
  i {
    font-size: 50px;
  }
  span {
    position: absolute;
    bottom: 10%;
    left: 50%;
    transform: translateX(-50%);
    font-family: raleway;
  }
`;
class MenuButton extends React.Component {
  onClickBtn = () => {
    this.props.showPage(this.props.index);
  };
  render() {
    return (
      <ButtonStyle onClick={this.onClickBtn}>
        <Icon name={this.props.icon} />
        <span>{this.props.text}</span>
      </ButtonStyle>
    );
  }
}

export default MenuButton;
