import React, { Component } from "react";
import styled from "styled-components";
import { FlexBox } from "../reusuarble/Styles";
import { Container } from "../reusuarble/Styles";

const IntroContainer = styled.div`
  h2 {
    font-family: raleway;
    font-size: 120px;
    line-height: 100px;
    color: #6e7783;
    animation: rotation 0.5s forwards;
  }
  h2:nth-child(2),
  h2:nth-child(3) {
    animation-delay: 0.5s;
    opacity: 0;
  }
  h2:nth-child(3),
  h2:nth-child(4) {
    font-family: Hans;
    font-size: 110px;
    line-height: 110px;
    color: #fff;
  }
  @keyframes rotation {
    0% {
      -webkit-transform: rotateX(270deg);
      opacity: 0;
    }
    100% {
      opacity: 1;
      -webkit-transform: rotateX(360deg);
    }
  }
  .title {
    position: absolute;
    bottom: 100px;
    right: 0;
    text-align: right;
    color: #6e7783;
  }
  .title span {
    font-family: Hans;
    font-size: 50px;
    line-height: 60px;
  }
  h3 {
    font-family: raleway;
    color: #77aaad;
    font-weight: 200;
    font-size: 26px;
    position: absolute;
    bottom: 0;
  }

  h4 {
    font-size: 20px;
  }
  span {
    font-size: 16px;
  }
`;

class Intro extends Component {
  render() {
    return (
      <div>
        <Container>
          <IntroContainer>
            <FlexBox>
              <div className="flex-6">
                <h2>Break</h2>
                <h2>the Limit</h2>
                <h2>나의 한계를</h2>
                <h2>뛰어넘다</h2>
                <h3>Web Publisher / Front-end Developer</h3>
              </div>
              <div className="flex-5 title">
                <span>
                  안녕하세요
                  <br />
                  "이유림" 의
                  <br />
                  포트폴리오 사이트입니다
                </span>
              </div>
            </FlexBox>
          </IntroContainer>
        </Container>
      </div>
    );
  }
}

export default Intro;
