import React, { Component } from "react";
import styled from "styled-components";
import { FlexBox } from "../reusuarble/Styles";
import mypic from "./rv.jpg";

const Namecard = styled.div`
  position: relative;
  top: 12vh;
  margin: 0 auto;
  width: 50%;
  height: 60vh;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
  background-color: rgba(255, 255, 255, 0.5);

  img {
    width: 70%;
    padding: 70px 10px 10px 10px;
    display: block;
    margin: 0 auto;
  }
  .index h2 {
    font-size: 35px;
    font-family: raleway;
    color: #6e7783;
    padding-top: 60px;
  }
  .index h3 {
    font-size: 28px;
    font-family: raleway;
    border-bottom: 1px solid #ccc;
    width: 80%;
  }
  .label {
    font-weight: bold;
    font-size: 20px;
    text-align: right;
    padding-right: 20px;
  }
  .data {
    font-size: 16px;
  }
  .data span {
    font-size: 14px;
    font-weight: bold;
    color: #555;
  }
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  .skills {
    padding: 20px 70px 0 70px;
  }
  h4 {
    font-size: 20px;
  }
  .single-chart {
    width: 33%;
    justify-content: space-around;
  }
  .single-chart svg {
    display: block;
    margin: 0 auto;
    max-height: 100px;
  }

  .circle-bg {
    fill: none;
    stroke: #eee;
    stroke-width: 5.2;
  }

  .circle {
    fill: none;
    stroke-width: 5;
    animation: progress 1s ease-out forwards;
  }

  @keyframes progress {
    0% {
      stroke-dasharray: 0 100;
    }
  }

  .percentage {
    font-family: sans-serif;
    font-size: 8px;
    text-anchor: middle;
    font-weight: bold;
  }
`;

class About extends Component {
  render() {
    this.state = {
      per: {
        path:
          "M18 2.0845 a 15.9155 15.9155 0 0 1 0 31.831 a 15.9155 15.9155 0 0 1 0 -31.831",
      },
    };
    return (
      <Namecard>
        <FlexBox>
          <div className="flex-4">
            <img src={mypic} alt="프로필 사진" />
          </div>
          <div className="flex-6 index">
            <h2>Web Publisher</h2>
            <h3>이&nbsp;유&nbsp;림</h3>
            <FlexBox style={{ paddingTop: "20px" }}>
              <div className="flex-2 label">SCHOOL</div>
              <div className="flex-7 data">
                경복비즈니스고등학교<span>&nbsp;&nbsp;IT비즈니스과</span>
              </div>
            </FlexBox>
            <FlexBox style={{ paddingTop: "20px" }}>
              <div className="flex-2 label">EDUCATION</div>
              <div className="flex-7 data">
                SBS아카데미컴퓨터학원<span>&nbsp;&nbsp;웹퍼블리싱 과정</span>
              </div>
            </FlexBox>
            <FlexBox style={{ paddingTop: "20px" }}>
              <div className="flex-2 label">ADDRESS</div>
              <div className="flex-7 data">서울시 양천구</div>
            </FlexBox>
            <FlexBox style={{ paddingTop: "20px" }}>
              <div className="flex-2 label">PHONE</div>
              <div className="flex-7 data">010-7189-6838</div>
            </FlexBox>
            <FlexBox style={{ paddingTop: "20px" }}>
              <div className="flex-2 label">E-MAIL</div>
              <div className="flex-7 data">yeol0324@naver.com</div>
            </FlexBox>
          </div>
        </FlexBox>
        <div className="skills">
          <h4>I Can Do This</h4>
          <FlexBox>
            <div class="single-chart">
              <svg viewBox="0 0 40 40">
                <path class="circle-bg" d={this.state.per.path} />
                <path
                  class="circle"
                  stroke-dasharray="75, 100"
                  d={this.state.per.path}
                  style={{ stroke: "#2fa3f7" }}
                />
                <text x="18" y="20.35" class="percentage">
                  PS
                </text>
              </svg>
            </div>

            <div class="single-chart">
              <svg viewBox="0 0 40 40">
                <path class="circle-bg" d={this.state.per.path} />
                <path
                  class="circle"
                  stroke-dasharray="90, 100"
                  d={this.state.per.path}
                  style={{ stroke: "#e45e30" }}
                />
                <text x="18" y="20.35" class="percentage">
                  HTML/CSS
                </text>
              </svg>
            </div>

            <div class="single-chart">
              <svg viewBox="0 0 40 40">
                <path class="circle-bg" d={this.state.per.path} />
                <path
                  class="circle"
                  stroke-dasharray="75, 100"
                  d={this.state.per.path}
                  style={{ stroke: "#1a71b5" }}
                />
                <text x="18" y="20.35" class="percentage">
                  JQUERY
                </text>
              </svg>
            </div>

            <div class="single-chart">
              <svg viewBox="0 0 40 40">
                <path class="circle-bg" d={this.state.per.path} />
                <path
                  class="circle"
                  stroke-dasharray="60, 100"
                  d={this.state.per.path}
                  style={{ stroke: "#efd81d" }}
                />
                <text x="18" y="20.35" class="percentage">
                  JS
                </text>
              </svg>
            </div>

            <div class="single-chart">
              <svg viewBox="0 0 40 40">
                <path class="circle-bg" d={this.state.per.path} />
                <path
                  class="circle"
                  stroke-dasharray="50, 100"
                  d={this.state.per.path}
                  style={{ stroke: "#48cef7" }}
                />
                <text x="18" y="20.35" class="percentage">
                  REACT
                </text>
              </svg>
            </div>
          </FlexBox>
        </div>
      </Namecard>
    );
  }
}

export default About;
